import * as graphql from "graphql-yoga";
import { importSchema } from 'graphql-import';
import models from '../database/models';
import resolvers from './resolvers';
import middlewares from './middlewares';
var session = require('express-session');
import * as dotenv from 'dotenv';
dotenv.config();
export var startServer = function () {
    var context = function (req) { return ({
        models: models,
        req: req.request
    }); };
    var port = process.env["DEV_PORT_" + process.env.DEV_TYPE] || process.env.PRO_PORT;
    var options = {
        cors: {
            origin: false
        },
        port: port,
        endpoint: '/graphql',
        playground: process.env.DEV_TYPE === 'server' ? '/playground' : false
    };
    var server = new graphql.GraphQLServer({
        context: context,
        resolvers: resolvers,
        middlewares: middlewares,
        typeDefs: importSchema('server/graphql/schemas/index.graphql')
    });
    server.express.use(session({
        secret: "some-random-secret-here",
        resave: true,
        saveUninitialized: true,
        cookie: {
            secure: process.env.NODE_ENV === 'production',
            maxAge: 1000 * 60 * 60 * 12
        }
    }));
    server.start(options, function () {
        console.log("\uD83D\uDE80\uD83D\uDE80\uD83D\uDE80 Server is running on  http://localhost:" + port);
    });
    return server;
};



