import * as mongoose from 'mongoose';
export var startDB = function () {
    var url = process.env["MONGODB_URI_" + process.env.NODE_ENV];
    mongoose.connect(url, { useNewUrlParser: true }, function () {
        console.log("\uD83D\uDE80\uD83D\uDE80\uD83D\uDE80 Mongoose is running on " + url);
    });
};



